import _ from "lodash";
import React from "react";
import { defaultRules, parserFor, reactFor, ruleOutput } from "simple-markdown";
import ImageRenderer from "../Components/Chat/MessageComponents/ImageRenderer";
import { ActionTypes } from "./Constants";
import { dispatcher } from "./Dispatcher";
import EmoteStore from "./Types/Stores/EmoteStore";
import UserStore from "./Types/Stores/UserStore";

export default new class MessageFormatter {
	rules = _.extend(
		{},
		defaultRules,
		{
			image: {
				order: 10,
				match: src => /^https:\/\/(\S?)+\.(jpe?g|png|gif|bmp)\S*(\s|$)/i.exec(src),
				parse: (capture, _, state) => ({
					src: capture[0].trim(),
					message: state?.message,
					state
				}),
				react: (node, _, state) => (
					node.state.minimal
						? <span className="MinimalTag" key={state.key}>[image]</span>
						: <ImageRenderer key={state.key} src={node.src} message={node.message} />
				)
			},
			userMention: {
				order: 10,
				match: src => /^@(\S{28})/.exec(src),
				parse: (capture, _, state) => ({ userId: capture[1], state }),
				react: (node, _, state) => {
					dispatcher.useForceUpdater(
						ActionTypes.USER_UPDATE,
						({ user }) => user.id === node.userId
					);

					const user = UserStore.getUser(node.userId);

					return user ? (
						React.createElement(
							user.Mention,
							{ key: state.key }
						)
					) : null;
				}
			},
			emote: {
				order: 10,
				match: src => /^:(\S+)\.(\S+):/.exec(src),
				parse: (capture, _, state) => ({ packId: capture[1], emoteId: capture[2], src: capture.src, state }),
				react: (node, _, state) => {
					const emote = EmoteStore.getEmote(node.packId, node.emoteId);

					return emote ? (
						React.createElement(
							emote.Component,
							{ key: state.key }
						)
					) : null;
				}
			},
			video: {
				order: 10,
				match: src => /^https:\/\/(\S?)+\.(mp4|webm)(\S?)+/.exec(src),
				parse: (capture, _, state) => ({ src: capture[0], state }),
				react: (node, _, state) => (
					// TODO add custom video component
					<video
						key={state.key}
						src={node.src}
						controls
					/>
				)
			},
			audio: {
				order: 10,
				match: src => /^https:\/\/(\S?)+\.(ogg|mp3|wav)(\S?)+/.exec(src),
				parse: (capture, _, state) => ({ src: capture[0], state }),
				react: (node, _, state) => (
					// TODO add custom audio component
					<audio
						key={state.key}
						src={node.src}
						controls
					/>
				)
			},
			youtube: {
				order: 10,
				match: src => /^https:\/\/(www\.youtube\.com\/watch\?v=|youtu\.be\/)(\S+)/i.exec(src),
				parse: (capture, _, state) => ({ id: capture[2], state }),
				react: (node, _, state) => (
					<iframe
						key={state.key}
						className="YouTubeEmbed"
						src={`https://www.youtube.com/embed/${node.id}`}
						allowFullScreen
					/>
				)
			}
			// TODO add file renderer
			// TODO add replies
			// TODO add color tags
			// TODO add size attribute
			// TODO add group mention
			// TODO add list items
			// TODO add polls
			// TODO add code blocks
			// TODO add invite embeds
			// TODO add link embeds
		}
	);

	parser = parserFor(this.rules);
	render = reactFor(ruleOutput(this.rules, "react"));

	parse(content, message, props = {}) {
		return this.render(
			this.parser(
				content,
				{
					inline: true,
					message,
					...props
				}
			)
		);
	}
}