import _ from "lodash";
import { appFocusState as appIsFocused } from "../../../App";
import { ActionTypes, playSound } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import GroupStore from "./GroupStore";
import UserStore from "./UserStore";

const cache = {};
const readCounts = {};
const listeners = {};

let readCountListenerInitialized = false;

const MessageCountStoreClass = class MessageCountStore extends Store {
	async _initializeReadListener() {
		let localUser;
		while (!(localUser = UserStore.getLocalUser()))
			await new Promise(r => setTimeout(r, 250));

		Database.createSnapshotListener(
			Database.doc("usersettings", localUser.id, "v2", "readCounts"),
			data => {
				data = Database.serializeObject(data);

				for (const circleId in data) {
					for (const groupId in data[circleId]) {
						dispatcher.dispatch({
							type: ActionTypes.UPDATE_READ_COUNT,
							circleId, groupId,
							value: data[circleId][groupId],
							fromDb: true
						});
					}
				}

				readCountListenerInitialized = true;
			}
		);
	}

	_initializeListenerFor(circleId) {
		if (listeners[circleId]) return;

		listeners[circleId] = Database.createSnapshotListener(
			Database.doc("messageCounts", circleId),
			data => {
				data = Database.serializeObject(data);

				const selectedGroup = GroupStore.getSelectedGroup();

				for (const groupId in data) {
					if (groupId === "t") continue;

					dispatcher.dispatch({
						type: ActionTypes.UPDATE_MESSAGE_COUNT,
						circleId, groupId,
						value: data[groupId] || 0
					});

					if (selectedGroup?.id === groupId && appIsFocused) {
						this.markRead(circleId, groupId);
					}
				}

				dispatcher.dispatch({
					type: ActionTypes.UPDATE_MESSAGE_COUNT,
					circleId,
					groupId: "__total",
					value: data.t || 0
				});

				if (selectedGroup?.groupId === circleId && appIsFocused) {
					this.markRead(circleId);
				}
			}
		);
	}

	getMessageCount(circleId, groupId = null) {
		if (!listeners[circleId]) this._initializeListenerFor(circleId);

		return _.get(cache, [circleId, groupId ?? "__total"]) || 0;
	}

	getUnreadMessageCount(circleId, groupId = null) {
		if (!listeners[circleId]) this._initializeListenerFor(circleId);

		let total = 0;
		let read = 0;

		if (groupId == null && !~circleId.split("_").indexOf(UserStore.getLocalUser().id)) {
			for (const groupId in cache[circleId] ?? {}) {
				if (groupId === "__total") continue;

				total += this.getMessageCount(circleId, groupId);
				read += _.get(readCounts, [circleId, groupId], 0);
			}
		}
		else {
			total = this.getMessageCount(circleId, groupId);
			read = _.get(readCounts, [circleId, groupId ?? "__total"]);
		}

		if ((!read || total < read) && total !== read) {
			_.set(readCounts, [circleId, groupId], total);

			return total;
		}

		return total - read <= 0 ? 0 : total - read;
	}

	markRead(circleId, groupId = null, value = null) {
		dispatcher.dispatch({
			type: ActionTypes.UPDATE_READ_COUNT,
			circleId,
			groupId: groupId ?? "__total",
			value: value ?? this.getMessageCount(circleId, groupId)
		});
	}
}

const updateDatabase = _.debounce(() => {
	if (!readCountListenerInitialized) return;

	Database.set(
		Database.doc("usersettings", UserStore.getLocalUser().id, "v2", "readCounts"),
		readCounts
	);
}, 3000);

const MessageCountStore = new MessageCountStoreClass(dispatcher, {
	[ActionTypes.UPDATE_MESSAGE_COUNT]: ({ circleId, groupId, value }) => {
		_.set(cache, [circleId, groupId], value);
	},
	[ActionTypes.UPDATE_READ_COUNT]: ({ circleId, groupId, value, fromDb }) => {
		_.set(readCounts, [circleId, groupId], value);

		!fromDb && updateDatabase();
	}
});

MessageCountStore._initializeReadListener();

export default MessageCountStore;