import React from "react";
import "./DmsPage.scss";
import { appHashArgs } from "../../App";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import GroupStore from "../../Classes/Types/Stores/GroupStore";
import GroupsPanel from "../Panels/GroupsPanel";
import ChatPanel from "../Panels/ChatPanel";

function renderGroups(_groups) {
	if (!_groups) return null;
	const groups = Object.values(_groups).sort((a, b) => b.lastMessageDate - a.lastMessageDate);

	return groups.map(group => (
		<group.Component key={group.groupId} />
	));
}

export default function DmsPage() {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);
	dispatcher.useForceUpdater(ActionTypes.BATCH_UPDATE_DMS);

	const groups = GroupStore.getGroups("dms");

	function RightHandRenderer() {
		switch (appHashArgs[1]?.trim() ?? "") {
			case "": {
				return null;
			}

			default: {
				return (
					<ChatPanel
						circleId="dms"
						groupId={appHashArgs[1]}
					/>
				);
			}
		}
	}

	console.log(groups);

	return (
		<div className="DmsPage Flex">
			<GroupsPanel>
				{renderGroups(groups)}
			</GroupsPanel>

			<RightHandRenderer />
		</div>
	);
}