import _ from "lodash";
import React from "react";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";

export default function GroupsPanel({ children }) {
	const ref = React.useRef();

	const handleScaler = React.useMemo(() => e => {
		dispatcher.dispatch({
			type: ActionTypes.UPDATE_CSS_VAR,
			key: "groupPanelSize",
			value: _.clamp(
				e.clientX - ref.current.getBoundingClientRect().x,
				100, 500
			)
		});
	}, [children]);

	const events = {
		onMouseDown: () => {
			document.addEventListener("mousemove", handleScaler);

			let remove;
			document.addEventListener("mouseup", remove = () => {
				document.removeEventListener("mousemove", handleScaler);
				document.removeEventListener("mouseup", remove);
			});
		}
	};

	return (
		<div className="Panel PanelGroups" ref={ref}>
			{children}

			<div className="GroupsScaler" {...events} />
		</div>
	);
}