import _ from "lodash";
import { ActionTypes, UserStatus } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import User from "../User";
import CirlosLogo from "../../../Assets/Logo.svg";

let localUser;
const cache = {
	"SYSTEM": new User({
		id: "SYSTEM",
		displayName: "System",
		status: UserStatus.BOT,
		avatarUrl: CirlosLogo,
		profileBackgroundNsfw: "https://c.tenor.com/Z6gmDPeM6dgAAAAC/dance-moves.gif",
		feed: "Giving friendly welcomes and farewells. ♥"
	})
};

const UserStoreClass = class UserStore extends Store {
	getLocalUser() {
		return localUser;
	}

	getUser(userId) {
		return cache[userId] ?? (
			this.fetchUser(userId),
			cache[userId] = new User({
				id: userId,
				loading: true,
				displayName: "..."
			})
		);
	}

	getUsers() {
		return cache;
	}

	fetchUser(userId, oneTime = false) {
		if (!userId) return null;

		return new Promise(r => {
			const unbind = Database.createSnapshotListener(
				Database.doc("userdata", userId),
				doc => {
					if (cache[userId]?.loading)
						delete cache[userId];

					const user = doc
						? _.extend(cache[userId] ?? new User(), doc)
						: new User({
							id: userId,
							displayName: "Deleted User"
						});

					dispatcher.dispatch({
						type: ActionTypes.USER_UPDATE,
						user
					});

					oneTime && unbind();

					r(user);
				}
			);
		});
	}
}

const UserStore = new UserStoreClass(dispatcher, {
	[ActionTypes.LOCAL_USER_UPDATE]: ({ user }) => {
		localUser = user;
	},
	[ActionTypes.USER_UPDATE]: ({ user }) => {
		cache[user.id] = user;
	}
});

export default UserStore;