import React from "react";
import { AlertOctagon } from "react-feather";
import { useMounted } from "../../../Classes/Hooks";
import InlineLoading from "../../InlineLoading";
import { openImageModal } from "../../Modals";
import "./Styles/ImageRenderer.scss";

export default function ImageRenderer({ src, message = null }) {
	const [loaded, setLoaded] = React.useState(false);
	const [failed, setFailed] = React.useState(false);
	const isMounted = useMounted();

	React.useEffect(() => {
		const image = Object.assign(
			document.createElement("img"),
			{
				style: "display:none",
				src,
				onload: () => (isMounted && setLoaded(true), image.remove()),
				onerror: () => (isMounted && setFailed(true), image.remove())
			}
		);

		document.body.appendChild(image);

		return () => image.remove();
	}, [src]);

	return (
		<div
			className="ImageContainer FlexCenter"
			onClick={() => openImageModal(src, () =>
				[...document.querySelectorAll(".ImageContainer > .Image")]
					.reverse().map(e => e.src))}
		>
			{failed
				? (
					<div className="FailedPlaceholder FlexCenter">
						<AlertOctagon />
						<h4>Image 404</h4>
					</div>
				)
				: (
					loaded
						? (
							<img
								className="Image"
								src={src}
							/>
						)
						: (
							<div className="LoadingPlaceholder FlexCenter">
								<InlineLoading />
							</div>
						)
				)}
		</div>
	);
}