import React from "react";
import "./Home.scss";

import { ReactComponent as WaveVector } from "../Assets/WaveVector.svg";
import * as Feather from "react-feather";
import { Modals } from "../Components/Modals";
import AuthModal from "../Components/Modals/AuthModal";
import UserStore from "../Classes/Types/Stores/UserStore";
import PageFooter from "../Components/PageElements/PageFooter";

export default function HomePage() {
	const localUser = UserStore.useState(UserStore.getLocalUser);

	return (
		<div className="HomePage">
			<div className="HomeBackgroundContainer">
				<div className="HomeBackground">
					<WaveVector />
					<WaveVector />
				</div>
			</div>

			<div className="Hero">
				<h1>Cirlos</h1>

				<h3>
					A simpler way to connect with friends and family,<br />
					with powerful ways to personalize your experience.<br />
				</h3>

				<div className="Buttons">
					{localUser !== null
						? <React.Fragment>
							<div
								className="Button Focus"
								onClick={() => window.location.assign("#app")}
							>
								<Feather.CheckCircle />

								Continue To App
							</div>
						</React.Fragment>

						: <React.Fragment>
							<div
								className="Button Focus"
								onClick={() => Modals.push(<AuthModal />)}
							>
								<Feather.Edit />

								Sign Up
							</div>

							<div
								className="Button"
								onClick={() => Modals.push(<AuthModal />)}
							>
								<Feather.LogIn />

								Sign In
							</div>
						</React.Fragment>}

					<div className="Button">
						<Feather.Download />

						Download
					</div>
				</div>
			</div>
		</div >
	);
}