import _ from "lodash";
import { ActionTypes } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import Emote from "../Emote";

const cache = {};

export const EmoteStoreClass = class EmoteStore extends Store {
	getEmote(packId, emoteId) {
		return _.get(cache, [packId, emoteId]) ?? (
			this.fetchEmote(packId, emoteId),
			_.set(cache, [packId, emoteId], new Emote({ packId, id: emoteId, loading: true })),
			_.get(cache, [packId, emoteId])
		);
	}

	getCachedEmotes() {
		return cache;
	}

	async fetchEmote(packId, emoteId) {
		const doc = await Database.getDoc(
			Database.doc("emotepacks", packId, "emotes", emoteId)
		);

		const emote = _.extend(
			_.get(cache, [packId, emoteId]) ?? new Emote(),
			{
				...doc,
				packId,
				loading: false
			}
		);

		dispatcher.dispatch({
			type: ActionTypes.FETCH_EMOTE,
			packId, emoteId, emote
		});

		return emote;
	}
}

const EmoteStore = new EmoteStoreClass(dispatcher, {
	[ActionTypes.FETCH_EMOTE]: ({ packId, emoteId, emote }) => {
		_.set(cache, [packId, emoteId], emote);
	}
});

export default EmoteStore;