import _ from "lodash";
import React from "react";
import { appHashArgs } from "../../App";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import ChatPanel from "../Panels/ChatPanel";
import GroupsPanel from "../Panels/GroupsPanel";
import CircleOverview from "./CircleOverview";
import "./CirclePage.scss";

export default function CirclePage({ circle }) {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);

	dispatcher.useForceUpdater(
		ActionTypes.CIRCLE_UPDATE_GROUPS,
		e => e.circle.id === circle.id,
		[circle]
	);

	function RightHandRenderer() {
		switch (appHashArgs[2]) {
			case "groups": {
				return <ChatPanel circleId={circle.id} groupId={appHashArgs[3]} />;
			}

			case "overview": {
				return <CircleOverview circleId={circle.id} />
			}
		}

		return null;
	}

	return (
		<div className="CirclePage Flex">
			<GroupsPanel>
				{circle.renderGroups()}
			</GroupsPanel>

			<RightHandRenderer />
		</div>
	);
}