import _ from "lodash";
import React from "react";
import { ActionTypes } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import UserStore from "./UserStore";

const styles = {
	"group-panel-size": 250
};

function toKebabCase(camelCase) {
	return camelCase.replace(/([a-z0-9]|(?=[A-Z]))([A-Z])/g, "$1-$2").toLowerCase();
}

const CssStoreClass = class CssStore extends Store {
	constructor(...args) {
		super(...args);

		const localUserListener = () => {
			const localUser = UserStore.getLocalUser();

			if (localUser) {
				this._databaseReference = Database.doc("usersettings", UserStore.getLocalUser().id, "v2", "cssVars");

				Database.getDoc(
					this._databaseReference,
				).then(data => {
					this._hasLoaded = true;

					dispatcher.dispatch({
						type: ActionTypes.BATCH_UPDATE_CSS_VARS,
						entries: data ?? {}
					});
				});

				UserStore.removeListener(localUserListener);
			}
		};

		UserStore.addListener(localUserListener);

		this._save = _.debounce(this._save.bind(this), 5000);
	}

	_save() {
		if (!this._hasLoaded) return;

		Database.set(this._databaseReference, styles)
			.catch(err => console.error("Error saving CSS variables!", err.stack));
	}

	getStyle(key) {
		return styles[toKebabCase(key)];
	}

	getStyles() {
		return styles;
	}

	get Renderer() {
		return () => {
			dispatcher.useForceUpdater(ActionTypes.UPDATE_CSS_VAR);
			dispatcher.useForceUpdater(ActionTypes.BATCH_UPDATE_CSS_VARS);

			return React.createElement(
				"style",
				{},
				`#root {
					${Object.entries(styles).map(([key, value]) => `--${key}: ${value};`).join("\n")}
				}`
			);
		};
	}
}

const CssStore = new CssStoreClass(dispatcher, {
	[ActionTypes.UPDATE_CSS_VAR]: ({ key, value }) => {
		styles[toKebabCase(key)] = value;

		CssStore._save();
	},
	[ActionTypes.BATCH_UPDATE_CSS_VARS]: ({ entries }) => {
		for (const key in entries) {
			const types = ["string", "number"];
			if (!~types.indexOf(typeof (key)) || !~types.indexOf(typeof (entries[key])))
				continue;

			styles[key] = entries[key];
		}
	}
});

export default CssStore;