import React from "react";
import { Film, MessageSquare, Send } from "react-feather";
import GroupStore from "../../Classes/Types/Stores/GroupStore";
import Toasts from "../Toasts";
import "./ChatTextArea.scss";
import RichTextArea from "./RichTextArea";

export default function ChatTextArea() {
	const events = {
		handleSend: async message => {
			if (message.trim()) {
				const group = GroupStore.getSelectedGroup();
				if (!group) return Toasts.showToast("No selected group!", "Failure");

				group.sendMessage(message);
			}

			return true;
		}
	};

	return (
		<div className="ChatTextAreaContainer Flex">
			<RichTextArea
				id="ChatTextArea"
				className="ChatTextArea"
				onSend={events.handleSend}
			><MessageSquare /> Say hi! </RichTextArea>

			<div className="Button">
				<Film />
			</div>

			<div className="Button" onClick={() => (
				document.getElementById("ChatTextArea").dispatchEvent(
					new KeyboardEvent("keydown", {
						key: "Enter",
						shiftKey: false,
						bubbles: true
					})
				)
			)}>
				<Send />
			</div>
		</div>
	)
}