import _ from "lodash";
import { ActionTypes } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import Circle from "../Circle";

const cache = {};

const CircleStoreClass = class CircleStore extends Store {
	getCircle(circleId) {
		return cache[circleId] ?? (
			this.fetchCircle(circleId),
			new Circle({
				id: circleId,
				loading: true,
				name: "..."
			})
		);
	}

	getCircles() {
		return cache;
	}

	async fetchCircle(circleId, oneTime = false) {
		return new Promise(r => {
			const unbind = Database.createSnapshotListener(
				Database.doc("circles", circleId),
				doc => {
					const circle = doc
						? _.extend(cache[circleId] ?? new Circle(), doc)
						: new Circle({
							id: circleId,
							name: "Invalid Circle"
						});

					dispatcher.dispatch({
						type: ActionTypes.CIRCLE_UPDATE,
						circle
					});

					oneTime && unbind();

					r(circle);
				}
			);
		});
	}
}

const CircleStore = new CircleStoreClass(dispatcher, {
	[ActionTypes.CIRCLE_UPDATE]: ({ circle }) => {
		cache[circle.id] = circle;
	},
	[ActionTypes.CIRCLE_UPDATE_GROUPS]: ({ circle, groups }) => {
		cache[circle.id]._groups = groups;
	}
});

export default CircleStore;