import _ from "lodash";
import { formatDateString } from "../Constants";
import MessageFormatter from "../MessageFormatter";

export default class Message {
	id = "";
	content = "";
	senderId = "";
	creationDate = null;
	mentions = [];

	constructor(message) {
		_.extend(this, message);
	}

	getContent(props = {}) {
		return MessageFormatter.parse(this.content, this, props);
	}

	getCreationDate(_time) {
		if (!_time && !this.creationDate) return "";
		const time = _time?.toDate?.() ?? _time ?? this.creationDate.toDate();

		return `${formatDateString(time)} at ${time.toLocaleTimeString("en-US")}`
	}
}