import _ from "lodash";
import React from "react";
import "./RichTextArea.scss";
import { getRandomKey, joinClassNames } from "../../Classes/Constants";
import { useEventListener } from "../../Classes/Hooks";
import MessageFormatter from "../../Classes/MessageFormatter";

export default function RichTextArea(props) {
	const containerRef = React.useRef();
	const ref = React.useRef();

	const [value, setValue] = React.useState(props.defaultValue ?? "");
	const [focused, setFocused] = React.useState(false);
	const [selection, setSelection] = React.useState([0, 0]);

	const caret = React.useMemo(() =>
		<div className="Caret" key={getRandomKey()} />, [value, selection]);

	const events = {
		onInput: e => {
			props.onInput?.(e);

			setValue(e.target.value);
		},
		onKeyDown: async e => {
			props.onKeyDown?.();

			switch (e.key) {
				case "Enter": {
					if (!e.shiftKey) {
						e.preventDefault();

						if ((await props.onSend?.(value)) === true) {
							setValue("");
							ref.current.value = "";
						}
					}
				} break;
			}
		},
		onFocus: () => setFocused(true),
		onBlur: () => setFocused(false)
	};

	useEventListener("selectionchange", () => {
		const selection = document.getSelection();

		if (selection?.anchorNode === containerRef.current) {
			setSelection([
				ref.current.selectionStart,
				ref.current.selectionEnd
			]);
		}
	});

	return (
		<div
			ref={containerRef}
			className={joinClassNames("RichTextArea", [focused, "Focused"])}
			onClick={() => setImmediate(() => ref.current.focus())}
		>
			<textarea
				ref={ref}
				{..._.omit(
					props,
					"children", "onInput", "onChange", "onSend",
					"onKeyDown"
				)}
				{...events}
			/>

			{value
				? (
					<div className="Renderer">
						{MessageFormatter.parse(value.slice(0, selection[0]))}

						{selection[1] - selection[0] > 0 && (
							<span className="SelectedText">
								{value.slice(selection[0], selection[1])}
							</span>
						)}

						{caret}

						{MessageFormatter.parse(value.slice(selection[1]))}
					</div>
				)
				: (
					<div className="Renderer Placeholder">
						{caret}
						{props.children}
					</div>
				)}
		</div>
	);
}