import React from "react";
import { appHashArgs } from "../../App";
import { getAcronym } from "../../Classes/Constants";
import CircleStore from "../../Classes/Types/Stores/CircleStore";
import UserStore from "../../Classes/Types/Stores/UserStore";
import { openImageModal } from "../Modals";
import "./CircleOverview.scss";

export default function CircleOverview({ circleId }) {
	const circle = CircleStore.getCircle(circleId);
	const owner = UserStore.useState(() => UserStore.getUser(circle?.ownerId));

	return circle ? (
		<div className="CircleOverview FlexCenter">
			<div className="Hero FlexCenter">
				<div
					className="Circle"
					onClick={() => openImageModal(circle.iconUrl)}
				>
					{circle.iconUrl
						? <img
							className="Icon"
							src={circle.iconUrl}
							alt={getAcronym(circle.name)}
						/>
						: <h3 className="Icon Acronym">
							{getAcronym(circle.name)}
						</h3>}
				</div>

				<div className="Banner">
					{circle.iconUrl && (
						// TODO add banner images
						<img
							className="BannerImage Default"
							src={circle.iconUrl}
							alt="banner"
						/>
					)}
				</div>

				<h1 className="Title">{circle.name}</h1>
			</div>
		</div>
	) : null;
}