import _ from "lodash";
import React from "react";
import { Users } from "react-feather";
import { appFocusState, pushHistoryState } from "../App";
import { ActionTypes, joinClassNames, playSound } from "../Classes/Constants";
import Database from "../Classes/Database";
import { dispatcher } from "../Classes/Dispatcher";
import { useEventListener, useOnMount } from "../Classes/Hooks";
import Circle from "../Classes/Types/Circle";
import CircleStore from "../Classes/Types/Stores/CircleStore";
import CssStore from "../Classes/Types/Stores/CssStore";
import GroupStore from "../Classes/Types/Stores/GroupStore";
import MessageCountStore from "../Classes/Types/Stores/MessageCountStore";
import UserStore from "../Classes/Types/Stores/UserStore";
import VoiceModule, { initializeVoiceModule } from "../Classes/VoiceModule";
import ChatTextArea from "../Components/Chat/ChatTextArea";
import ContextMenu from "../Components/ContextMenuHandler";
import InlineLoading from "../Components/InlineLoading";
import CorePanel from "../Components/Panels/CorePanel";
import Toasts from "../Components/Toasts";
import Tooltip from "../Components/Tooltip";
import AlertSound from "../Assets/Audio/alert.ogg";
import "./AppPage.scss";

export default function AppPage() {
	const localUser = UserStore.useState(UserStore.getLocalUser);

	if (localUser === null) {
		Toasts.showToast("You must be signed in to access the app", "Failure");

		window.location.assign(window.location.origin);
	}

	return localUser ? (
		<div className="AppPage">
			<Core localUser={localUser} />
		</div>
	) : null;
}

function Core({ localUser }) {
	const { items: circles } = Database.useSnapshot(
		Database.collection("userdata", localUser.id, "circles"),
		{
			// TODO handle circle stores
			processor: async doc => {
				const circle = CircleStore.getCircles()[doc.id] ?? await CircleStore.fetchCircle(doc.id);

				return _.assign(circle, doc);
			}
		}
	) ?? {};

	useOnMount(() => {
		VoiceModule.initialize();
	});

	useEventListener("focus", () => {
		const selectedGroup = GroupStore.getSelectedGroup();
		selectedGroup?.markAsRead();
	}, { target: window });

	if (circles === undefined) {
		return <LoadingOverlay />;
	}

	return (
		<div className="Core">
			<CssStore.Renderer />
			<LoadingOverlay hidden />

			<div className="Panel PanelMain Flex">
				<div className="Panel PanelCircles">
					<div className="Circles">
						{circles
							.sort((a, b) => a.position - b.position)
							.map(circle => circle instanceof Circle && (
								<circle.BubbleComponent key={circle.id} />
							))}
					</div>

					<DmBubbles />
				</div>

				<div className="Panel PanelCore">
					<CorePanel />
				</div>
			</div>

			<div className="Panel PanelFooter FlexCenter">
				<div className="Circle" onClick={() => pushHistoryState("#app/dms/")}>
					<Users className="Icon" />

					<Tooltip>DMs</Tooltip>
				</div>

				<ContextMenu.Wrapper
					menu={<localUser.ContextMenu />}
					wrapperClassName="LocalUserHead FlexCenter"
				>
					<div className="Circle">
						<localUser.AvatarComponent className="Icon" />
					</div>

					<div className="Details">
						<h4 className="Username">
							{localUser.displayName}
						</h4>

						<localUser.StatusText />
					</div>
				</ContextMenu.Wrapper>

				<div className="Divider" />

				<ChatTextArea />
			</div>
		</div>
	);
}

function LoadingOverlay({ hidden = false }) {
	return (
		<div className={joinClassNames("LoadingOverlay", "FlexCenter", [hidden, "Hidden"])}>
			<InlineLoading style={{ fontSize: "2em" }} />
		</div>
	);
}

function DmBubbles() {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);

	const localUser = UserStore.useState(() => UserStore.getLocalUser());

	dispatcher.useForceUpdater(
		ActionTypes.UPDATE_MESSAGE_COUNT,
		({ circleId }) => ~circleId.split("_").indexOf(localUser?.id)
	);

	dispatcher.useForceUpdater(
		ActionTypes.UPDATE_READ_COUNT,
		({ circleId }) => ~circleId.split("_").indexOf(localUser?.id)
	);

	dispatcher.useForceUpdater(ActionTypes.BATCH_UPDATE_DMS);

	const dms = GroupStore.getGroups("dms");
	const unreads = Object.values(dms ?? {}).map(dm => (
		[dm.id, MessageCountStore.getUnreadMessageCount(dm.groupId)]
	), {});

	return (
		<div className="DmBubbles">
			{unreads.map(([userId, unreadCount]) => unreadCount > 0 &&
				<DmBubble key={userId} userId={userId} unreadCount={unreadCount} />)}
		</div>
	);
}

function DmBubble({ userId, unreadCount }) {
	const user = UserStore.useState(() => UserStore.getUser(userId));

	const selectedGroup = GroupStore.getSelectedGroup();
	selectedGroup?.id === userId && MessageCountStore.markRead(selectedGroup.groupId);

	dispatcher.useForceUpdater(
		ActionTypes.USER_UPDATE,
		({ user }) => user.id === userId
	);

	React.useEffect(() => {
		if (selectedGroup?.id !== userId || !appFocusState) {
			playSound(AlertSound, 0.5);
		}
	}, [unreadCount]);

	return user ? (
		<div
			className="Circle DmBubble"
			key={userId}
			onClick={() => pushHistoryState(`#app/dms/${userId}`)}
		>
			{React.createElement(
				user.AvatarComponent,
				{ size: 40 }
			)}

			<div className="UnreadBlips Flex">
				<div className="Blip Mention">
					{unreadCount > 999
						? `${Math.round(unreadCount / 1000)}k`
						: unreadCount}
				</div>
			</div>

			<Tooltip direction="right">{user.displayName}</Tooltip>
		</div>
	) : null;
}