import { doc, limitToLast, orderBy, query } from "@firebase/firestore";
import _ from "lodash";
import { appHashArgs } from "../../../App";
import { ActionTypes } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import Group, { GroupDm } from "../Group";
import CircleStore from "./CircleStore";
import UserStore from "./UserStore";

const cache = {};
const groupListeners = {};

const GroupStoreClass = class GroupStore extends Store {
	getGroup(circleId, groupId) {
		if (circleId === "*") {
			for (const circleId in cache) {
				const group = cache[circleId][groupId];

				if (group) return group;
			}
		}

		return _.get(cache, [circleId, groupId]);
	}

	getSelectedGroup() {
		if (appHashArgs[0] === "dms")
			return this.getGroup("dms", appHashArgs[1]);

		return this.getGroup(appHashArgs[1], appHashArgs[3]);
	}

	getGroups(circleId) {
		if (!cache[circleId]) {
			this.fetchGroups(circleId);
		}

		return cache[circleId];
	}

	async fetchGroups(circleId) {
		if (groupListeners[circleId]) return;
		if (circleId === "dms") return await this.fetchDms();

		const circle = CircleStore.getCircle(circleId);
		if (!circle) return console.error(
			`Can't fetch groups for uncached circle with ID "${circleId}"!`
		);

		// TODO make sure this actually completes
		while (circle.loading)
			await new Promise(r => setTimeout(r, 100));

		return new Promise(r => {
			const unbind = Database.createSnapshotListener(
				Database.collection("circles", circleId, "groups"),
				({ items = [] } = {}) => {
					for (const doc of items) {
						const group = _.extend(
							_.get(cache, [circleId, doc.id]) ?? new Group(),
							doc,
							{
								parent: circle.groupPositions?.[doc.id]?.[0],
								position: circle.groupPositions?.[doc.id]?.[1]
							}
						);

						dispatcher.dispatch({
							type: ActionTypes.GROUP_UPDATE,
							circle,
							group
						});
					}

					dispatcher.dispatch({
						type: ActionTypes.CIRCLE_UPDATE_GROUPS,
						circle,
						groups: cache[circleId]
					});

					r(cache[circleId]);
				}
			);

			groupListeners[circleId] = unbind;
		});
	}

	async fetchDms() {
		let user;
		while (!(user = UserStore.getLocalUser()))
			await new Promise(r => setTimeout(r, 100));

		return new Promise(r => {
			const unbind = Database.createSnapshotListener(
				query(
					Database.collection("userdata", user.id, "dms"),
					orderBy("lastMessageDate", "desc")
				),
				({ items = [] } = {}) => {
					for (const doc of items) {
						const group = _.extend(
							_.get(cache, ["dms", doc.id]) ?? new GroupDm(),
							doc,
							{
								circleId: "dms",
								isDm: true,
								userId: doc.id
							}
						);

						dispatcher.dispatch({
							type: ActionTypes.DM_UPDATE,
							group,
							userId: doc.id
						});
					}

					dispatcher.dispatch({
						type: ActionTypes.BATCH_UPDATE_DMS,
						groups: cache.dms
					});

					r(cache.dms);
				}
			);

			groupListeners.dms = unbind;
		});
	}
}

const GroupStore = new GroupStoreClass(dispatcher, {
	[ActionTypes.GROUP_UPDATE]: ({ circle, group }) => {
		_.set(cache, [circle.id, group.id], group);
	},
	[ActionTypes.DM_UPDATE]: ({ group, userId }) => {
		_.set(cache, ["dms", userId], group);
	},
	[ActionTypes.UPDATE_VOICE_MEMBERS]: ({ groupId, userIds }) => {
		const group = GroupStore.getGroup("*", groupId);
		if (group) group.voiceMembers = userIds;
	}
});

export default GroupStore;