import _ from "lodash";
import { Modals } from "../Components/Modals";
import { ActionTypes } from "./Constants";
import { dispatcher } from "./Dispatcher";
import GroupStore from "./Types/Stores/GroupStore";
import UserStore from "./Types/Stores/UserStore";

export const VoiceEvents = {
	DISCONNECT: "disconnect",
	CONNECT: "connect",
	ANSWER: "ANSWER",
	PEERS: "PEERS",
	CONSUME: "CONSUME",
	NEW_PRODUCER: "NEW_PRODUCER",
	USER_LEFT: "user_left",
	UPDATE_USERS: "UPDATE_USERS"
};

const VoiceModule = new class VoiceModule {
	currentRoom = null;

	initialize() {
		return;

		if (this.initialized) return;
		this.initialized = true;

		const newSocket = () => new WebSocket(`ws://${window.location.hostname}:5000`);
		this.socket = newSocket();

		console.log("socket", this.socket);

		this.socket.addEventListener("message", ({ data }) => {
			const message = JSON.parse(data);

			console.log(
				"voice ws message",
				message
			);

			switch (message.type) {
				case VoiceEvents.UPDATE_USERS: {
					dispatcher.dispatch({
						type: ActionTypes.UPDATE_VOICE_MEMBERS,
						groupId: message.roomId,
						userIds: message.users
					});
				} break;
			}
		});

		this.socket.addEventListener("open", () => {
			this.socket.send(
				JSON.stringify({
					type: "init",
					userId: UserStore.getLocalUser().id
				})
			);
		});

		// this.socket.addEventListener("close", () => {
		// 	this.socket = newSocket();
		// });
	}
}

const iceOptions = {
	iceServers: [{
		urls: ["stun:192.99.69.216:46235"]
	}],
	iceCandidatePoolSize: 10
};

// TODO improve literally all of this because the person who wrote it has never used javascript
export class ChatRoom {
	clients = new Map();
	consumers = new Map();

	constructor(circleId, groupId) {
		const group = GroupStore.getGroup(circleId, groupId);

		if (!group) {
			console.error("Unable to find voice group!", { circleId, groupId });

			return null;
		}

		this.group = group;
	}

	async connect() {
		if (VoiceModule.currentRoom)
			await VoiceModule.currentRoom.disconnect();

		console.log("should connect");

		VoiceModule.socket.addEventListener("message", this.handleMessage.bind(this));
		await this.connectToSocket();

		VoiceModule.currentRoom = this;
	}

	async disconnect() {
		console.log("should disconnect");

		VoiceModule.currentRoom = null;

		VoiceModule.socket.send(JSON.stringify({
			type: VoiceEvents.DISCONNECT
		}));
	}

	createPeer() {
		return _.extend(
			new RTCPeerConnection(iceOptions),
			{
				onicecandidate: this.handleIceCandidate.bind(this),
				onnegotiationneeded: this.handleNegotiation.bind(this)
			}
		);
	}

	async connectToSocket() {
		const stream = await navigator.mediaDevices.getUserMedia({
			video: false,
			audio: true
		});

		this.handleRemoteTrack(stream, UserStore.getLocalUser().id);
		this.localStream = stream;

		this.localPeer = this.createPeer();
		this.localStream.getTracks().forEach(track =>
			this.localPeer.addTrack(track, this.localStream));

		await this.consumeAll();
	}

	async handleRemoteTrack(stream, userId) {
		console.log("remote tracK", stream, userId);

		const video = document.getElementById(userId) ?? document.createElement("video");

		video.id = userId;
		video.srcObject = stream;
		video.autoplay = true;
		video.muted = (userId === UserStore.getLocalUser().id);
		video.volume = 0.35;

		document.body.appendChild(video);
	}

	async handleIceCandidate({ candidate }) {
		if (candidate?.candidate?.length) {
			VoiceModule.socket.send(JSON.stringify({
				type: "ice",
				ice: candidate,
				userId: UserStore.getLocalUser().id,
				roomId: this.group.id
			}));
		}
	}

	async handleConsumerIceCandidate({ candidate }, id, consumerId) {
		if (candidate?.candidate?.length) {
			VoiceModule.socket.send(JSON.stringify({
				type: "consumer_ice",
				ice: candidate,
				consumerId,
				roomId: this.group.id
			}));
		}
	}

	handleConsume({ sdp, id, consumerId }) {
		const description = new RTCSessionDescription(sdp);

		this.consumers.get(consumerId).setRemoteDescription(description)
			.catch(console.error);
	}

	async createConsumeTransport(peer) {
		const consumerId = UserStore.getLocalUser().id;
		const consumerTransport = new RTCPeerConnection(iceOptions);

		this.clients.get(peer.userId).consumerId = consumerId;

		consumerTransport.userId = consumerId;
		consumerTransport.peer = peer;

		this.consumers.set(consumerId, consumerTransport);
		this.consumers.get(consumerId).addTransceiver("video", { direction: "recvonly" });
		this.consumers.get(consumerId).addTransceiver("audio", { direction: "recvonly" });

		const offer = await this.consumers.get(consumerId).createOffer();
		await this.consumers.get(consumerId).setLocalDescription(offer);

		this.consumers.get(consumerId).onicecandidate = e =>
			this.handleConsumerIceCandidate(e, peer.userId, consumerId);

		this.consumers.get(consumerId).ontrack = e =>
			this.handleRemoteTrack(e.streams[0], peer.userId);

		return consumerTransport;
	}

	async consumeOnce(peer) {
		const transport = await this.createConsumeTransport(peer);

		VoiceModule.socket.send(JSON.stringify({
			type: "consume",
			id: peer.userId,
			consumerId: transport.userId,
			sdp: await transport.localDescription,
			roomId: this.group.id
		}));
	}

	async handlePeers({ peers }) {
		for (const peer of peers) {
			this.handleNewProducer(peer);
		}
	}

	handleAnswer({ sdp }) {
		const description = new RTCSessionDescription(sdp);
		this.localPeer.setRemoteDescription(description)
			.catch(console.error);
	}

	async handleNewProducer({ userId }) {
		if (userId === UserStore.getLocalUser().id) return;

		this.clients.set(userId, { userId });
		await this.consumeOnce({ userId });
	}

	handleMessage({ data }) {
		const message = JSON.parse(data);

		switch (message.type) {
			case VoiceEvents.ANSWER:
				return this.handleAnswer(message);
			case VoiceEvents.PEERS:
				return this.handlePeers(message);
			case VoiceEvents.CONSUME:
				return this.handleConsume(message);
			case VoiceEvents.NEW_PRODUCER:
				return this.handleNewProducer(message);
			case VoiceEvents.USER_LEFT:
				return this.removeUser(message);
		}
	}

	removeUser() {

	}

	consumeAll() {
		VoiceModule.socket.send(JSON.stringify({
			type: "getPeers",
			userId: UserStore.getLocalUser().id,
			roomId: this.group.id
		}));
	}

	async handleNegotiation(peer, type) {
		const offer = await this.localPeer.createOffer();
		await this.localPeer.setLocalDescription(offer);

		VoiceModule.socket.send(JSON.stringify({
			type: VoiceEvents.CONNECT,
			sdp: this.localPeer.localDescription,
			userId: UserStore.getLocalUser().id,
			roomId: this.group.id
		}));
	}
}

export default VoiceModule;