import React from "react";
import { Check, Edit2, Edit3, Info, X } from "react-feather";
import { ActionTypes, joinClassNames, mib, openFileDialog, UserStatus } from "../../Classes/Constants";
import Database from "../../Classes/Database";
import { dispatcher } from "../../Classes/Dispatcher";
import MessageFormatter from "../../Classes/MessageFormatter";
import UserStore from "../../Classes/Types/Stores/UserStore";
import User from "../../Classes/Types/User";
import RichTextArea from "../Chat/RichTextArea";
import InlineLoading from "../InlineLoading";
import { Modals } from "../Modals";
import Toasts from "../Toasts";
import "./UserProfileModal.scss";

// TODO add change confirmation when closing modal
export default function UserProfileModal({ userId }) {
	const user = UserStore.useState(() => UserStore.getUser(userId));
	const localUser = UserStore.getLocalUser();

	const bgRef = React.useRef();
	const bgRefNsfw = React.useRef();

	const [editMode, setEditMode] = React.useState(false);
	const [isNsfwRevealed, revealNsfw] = React.useState(false);

	dispatcher.useForceUpdater(
		ActionTypes.USER_UPDATE,
		({ user }) => user.id === userId
	);

	async function selectProfileBackground(nsfw = false) {
		const [file] = await openFileDialog({ type: "image/*" });
		if (!file) return;

		if (file.size > mib(10))
			return Toasts.showToast(
				"You cannot upload a background above 10MiB",
				"Failure"
			);

		Database.uploadStorage(file, nsfw ? "profilebgNsfw" : "profilebg", localUser.id)
			.then(r => (
				localUser[nsfw ? "profileBackgroundNsfw" : "profileBackground"] = r.url,
				Toasts.showToast("Background updated - changes will show after confirming changes", "Warning", 10)
			))
			.catch(err => (console.error(err), Toasts.showToast("There was an error uploading your background image", "Failure")));
	}

	return (
		<div className="UserProfileModal Flex">
			<div className="Background FlexCenter">
				{user.profileBackground && <InlineLoading />}

				<img
					ref={bgRef}
					className="Image"
					src={user.profileBackground}
					onLoad={e => e.target.style.opacity = 1}
				/>

				{user.profileBackgroundNsfw && User.isNsfwVerified && (
					<img
						ref={bgRefNsfw}
						className="Image"
						src={user.profileBackgroundNsfw}
					/>
				)}
			</div>

			<div className="LeftSection Flex">
				<user.AvatarComponent size={250} />

				<h3 className="Username">{user.displayName}</h3>

				{editMode
					? (
						<RichTextArea defaultValue={user.feed} id="UserFeed">
							Specify what you're up to currently with your user feed.
						</RichTextArea>
					)
					: (
						user.feed && (
							<div className="Feed">
								{MessageFormatter.parse(user.feed)}
							</div>
						)
					)}

				<div className="AboutFields">
					{user.about?.map((about, index) => (
						<div className="AboutField" key={index}>
							<h3 className="Title">{about.title}</h3>
							<div className="Body">{about.field}</div>
						</div>
					))}
				</div>

				{editMode && (
					<React.Fragment>
						<h4
							className="Button"
							onClick={() => selectProfileBackground()}
						>Pick Background Image</h4>

						<h4
							className="Button"
							onClick={() => selectProfileBackground(true)}
						>Pick NSFW Background</h4>
					</React.Fragment>
				)}

				{user.profileBackgroundNsfw && User.isNsfwVerified && (
					<h4
						className="Button NsfwToggle"
						onClick={() => (
							revealNsfw(!isNsfwRevealed),
							bgRefNsfw.current.style.opacity = isNsfwRevealed ? 0 : 1
						)}
					>
						{isNsfwRevealed ? "Hide" : "Show"} NSFW Background
					</h4>
				)}
			</div>

			<div className="RightSection Flex">
				<div>
					<h1 className="Header">Bio</h1>
					<div className={joinClassNames("Bio MessageGroup", [editMode, "EditMode"])}>
						{editMode
							? (
								<RichTextArea defaultValue={user.bio} id="UserBio">
									Describe yourself with a personal bio.
								</RichTextArea>
							)
							: (
								user.bio
									? MessageFormatter.parse(user.bio)
									: (
										<div className="Placeholder">
											This user has no bio.
										</div>
									)
							)}
					</div>
				</div>
			</div>

			<div className="Buttons">
				{user === localUser && (
					editMode
						? (
							<Check
								className="Button"
								onClick={() => (
									setEditMode(false),
									Object.assign(
										localUser,
										{
											feed: document.getElementById("UserFeed").value,
											bio: document.getElementById("UserBio").value
										}
									),
									Database.update(
										localUser._ref,
										localUser
									).then(() => (
										Toasts.showToast(
											"Successfully updated profile"
										)
									))
								)}
							/>
						)
						: (
							<Edit3
								className="Button"
								onClick={() => setEditMode(true)}
							/>
						)
				)}

				<X
					className="Button"
					onClick={() => Modals.pop()}
				/>
			</div>
		</div>
	);
}